import React, { useState } from "react";
import { Link, Redirect } from "react-router-dom";
import { connect } from 'react-redux';
import { signup } from "../actions/auth";

const Signup = ({signup, isAuthenticated}) => {

    const [accountCreated, setAccountCreated] = useState(false);

    const [formData, setformData] = useState({
        name: '',
        email: '',
        password: '',
        re_password: '',
        address: ''
    });
    const { name,email, password, re_password, address } = formData;
    const onChange = e => setformData({ ...formData, [e.target.name]: e.target.value });
    const onSubmit = e => {
        e.preventDefault();
        if (password === re_password){
            signup(name,email,password, re_password, address);
            setAccountCreated(true);
        }
        
    };

    if (isAuthenticated){
        return <Redirect to='/home'/>
    }
    if (accountCreated){
        return <Redirect to='/login'/>
    }
    return (
        <div className='container mt-5'>
            <h1>Register</h1>
            <form onSubmit={e=>onSubmit(e)}> 
                <div className="mb-3">
                    <label htmlFor="exampleInputName" className="form-label">Name</label>
                    <input type="text" className="form-control" name='name'  onChange={e=>onChange(e)}  id="exampleInputName" required />
                </div>
                <div className="mb-3">
                    <label htmlFor="exampleInputEmail1" className="form-label">Email address</label>
                    <input type="email" className="form-control" name='email'  onChange={e=>onChange(e)}  id="exampleInputEmail1" required />
                </div>
                <div className="mb-3">
                    <label htmlFor="exampleInputPassword1" className="form-label">Password</label>
                    <input type="password" className="form-control" name='password'  onChange={e=>onChange(e)} id="exampleInputPassword1" required/>
                </div>
                <div className="mb-3">
                    <label htmlFor="exampleInputrePassword1" className="form-label">Confirm Password</label>
                    <input type="password" className="form-control" name='re_password'  onChange={e=>onChange(e)} id="exampleInputrePassword1" required/>
                </div>
                <div className="mb-3">
                    <label htmlFor="exampleInputAddress" className="form-label">Address</label>
                    <input type="text" className="form-control" name='address'  onChange={e=>onChange(e)}  id="exampleInputAddress" required />
                </div>
                <button type="submit" className="btn btn-primary">Submit</button>
            </form>
            <p className='my-3'>
                Already have an account! <Link to='/'>Login</Link>
            </p>
        </div>
    );
};

const mapStateToProps = state =>(
    {
        isAuthenticated: state.auth.isAuthenticated
    }
);

export default connect(mapStateToProps, { signup})(Signup);