import React, { useState } from "react";
import { Link, Redirect } from "react-router-dom";
import { connect } from 'react-redux';
import { login } from "../actions/auth";

const Login = ({login, isAuthenticated}) => {
    const [formData, setformData] = useState({
        email: '',
        password: ''
    });
    const { email, password } = formData;
    const onChange = e => setformData({ ...formData, [e.target.name]: e.target.value });
    const onSubmit = e => {
        e.preventDefault();
        login(email,password);
    };

    if (isAuthenticated){
        return <Redirect to='/home'/>
    }

    return (
        <div className='container mt-5'>
            <h1>Log-In</h1>
            <form onSubmit={e=>onSubmit(e)}> 
                <div className="mb-3">
                    <label htmlFor="exampleInputEmail1" className="form-label">Email address</label>
                    <input type="email" className="form-control" name='email'  onChange={e=>onChange(e)}  id="exampleInputEmail1" required />
                </div>
                <div className="mb-3">
                    <label htmlFor="exampleInputPassword1" className="form-label">Password</label>
                    <input type="password" className="form-control" name='password'  onChange={e=>onChange(e)} id="exampleInputPassword1" required/>
                </div>
                <button type="submit" className="btn btn-primary">Submit</button>
            </form>
            <p className='my-3'>
                Don't Have an account? <Link to='/signup'>Register Now</Link>
            </p>
        </div>
    )
};

const mapStateToProps = state =>(
    {
        isAuthenticated: state.auth.isAuthenticated
    }
);

export default connect(mapStateToProps, { login})(Login);